.download {
	display: inline-block;
	padding: 5px;
	min-width: 100px;
	text-align: center;
	cursor: pointer;
	color: white;
	margin-right: 10px;
	margin-bottom: 10px;
	border: 2px solid white;
	user-select: none;
}
.download[data-disabled=true] {
	opacity: 0.2;
}

.download:hover, .download:focus {
	background-color: rgb(74 144 226);
}