.text-field {
	color: white;
}
.text-field div {
	font-size: 0.8em;
}
.text-field input {
	background-color: rgb(39 50 56);
	border: none;
	border-bottom: 2px solid white;
	padding: 5px;
	font-size: 1em;
	color: white;
	margin-bottom: 10px;
	margin-right: 10px;
	min-width: 0;
	width: auto;
}

.text-field.error input {
	border-bottom: 2px solid rgb(245 67 54);
}